@use '../../selectors' as vars;
@use '../../shared/functions' as fn;
@use '../../shared/css-vars' as css-vars;

#{vars.$badge-selector} {
  display: inline-block;
  padding: fn.to-rem(4px) fn.to-rem(8px);
  // we use "em" here to change border radius when font size changes
  // Otherwise it is not responsives
  border-radius: fn.to-em(24px);

  white-space: nowrap;
}

#{vars.$badge-primary-selector} {
  color: css-vars.get-var('text-invert-color');
  background-color: css-vars.get-var('bg-invert-color');
}

#{vars.$badge-secondary-selector} {
  color: css-vars.get-color-var('black');
  background-color: css-vars.get-color-var('gray-20');
}

#{vars.$badge-success-selector} {
  color: css-vars.get-var('white');
  background-color: css-vars.get-var('success');
}

