@use '../shared/mixins' as mx;
@use '../config' as config;
@use '../shared/css-vars' as css-vars;
@use './fonts' as fonts;

// Register primary fonts
@include fonts.register-primary-fonts;

// Body styles
.#{config.$class-prefix}body {
  margin: 0; // Remove default margin
  color: css-vars.get-var('text-color');
  word-wrap: break-word; // ????

  @include mx.set-base-font-family;

  // Set font size and line height for MOBILE
  // Consider not to change font-suze for mobile
  // In sake of accessibility
