@use '../config' as config;

// Tab Selectors
$tabs-selector: '.#{config.$class-prefix}tabs' !default;
$tab-list-selector: '.#{config.$class-prefix}tab-list' !default;
$tab-selector: '.#{config.$class-prefix}tab' !default;
$tab-button-selector: '.#{config.$class-prefix}tab-button' !default;
$tab-active-selector: '.#{config.$class-prefix}tab-active' !default;
$tab-panel-selector: '.#{config.$class-prefix}tab-panel' !default;
$tab-panel-hidden-selector: '.#{config.$class-prefix}tab-panel-active' !default;

