@use '../selectors' as vars;
@use '../shared/css-vars' as css-vars;
@use '../shared/mixins' as mx;
@use '../shared/functions' as fn;

// Base
#{vars.$avatar-selector} {
  position: relative; // used for images positioned absolutely
  overflow: hidden; // used for images that overflow the avatar

  --diui-avatar-size: #{fn.to-rem(40px)};

  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: var(--diui-avatar-size);
  height: var(--diui-avatar-size);
  background-color: css-vars.get-color-var('light-gray');
  color: css-vars.get-color-var('text-color');
  border-radius: 50%;
}

// Icon
#{vars.$avatar-icon-selector} {
  width: 75% !important;
  height: 75% !important;
}

// Image
#{vars.$avatar-image-selector} {
  position: absolute;
  width: 100%;
  height: 100%;
}

// Sizes
#{vars.$avatar-xsmall-selector} {
  --diui-avatar-size: #{fn.to-rem(24px)};

  @include mx.base-font-configuration(14px, 20px);
}

#{vars.$avatar-small-selector} {
  --diui-avatar-size: #{fn.to-rem(32px)};

  @include mx.base-font-configuration(16px, 24px);
}

#{vars.$avatar-medium-selector} {
  --diui-avatar-size: #{fn.to-rem(40px)};

  @include mx.base-font-configuration(18px, 28px);
}

#{vars.$avatar-large-selector} {
  --diui-avatar-size: #{fn.to-rem(48px)};

  @include mx.base-font-configuration(20px, 24px);
}

#{vars.$avatar-xlarge-selector} {
  --diui-avatar-size: #{fn.to-rem(64px)};

  @include mx.base-font-configuration(24px, 28px);
}

// Shapes
#{vars.$avatar-square-selector} {
  border-radius: 0;
}

#{vars.$avatar-rounded-square-selector} {
  --diu-avatar-border-radius: #{fn.to-rem(10px)};

  border-radius: var(--diu-avatar-border-radius);

  &#{vars.$avatar-xsmall-selector} {
    --diu-avatar-border-radius: #{fn.to-rem(4px)};
  }

  &#{vars.$avatar-small-selector} {
    --diu-avatar-border-radius: #{fn.to-rem(8px)};
  }

  &#{vars.$avatar-medium-selector} {
    --diu-avatar-border-radius: #{fn.to-rem(10px)};
  }

  &#{vars.$avatar-large-selector} {
    --diu-avatar-border-radius: #{fn.to-rem(14px)};
  }

  &#{vars.$avatar-xlarge-selector} {
    --diu-avatar-border-radius: #{fn.to-rem(18px)};
  }
}
